.interview-main-div {
  padding: 20px 40px;
}

.bottom-video-div{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0,0,0,0.2); 
  z-index: 2; 
  padding: 0 20px;
  position: relative;
  bottom: 68px;
  height: 60px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.retake-button{
  width: 140px ;
  text-transform: none ;
  font-size: 14px ;
  background: #fff ;
  color: #04724d ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 25px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.retake-disabled-button{
  width: 140px ;
  text-transform: none ;
  font-size: 14px ;
  background: #00000029 ;
  color: #fff ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 25px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;

}


.timer-button{
  width: 80px ;
  text-transform: none ;
  font-size: 14px ;
  background: #fff ;
  color: #04724d ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 25px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.submit-button{
  width: 100px ;
  text-transform: none ;
  font-size: 14px ;
  color: #fff ;
  background-color: #04724d ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 25px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.submit-button-disabled{
  width: 100px ;
  text-transform: none ;
  font-size: 14px ;
  color: #fff ;
  background: #00000029 ; 
  font-weight: bold ;
  padding: 5px;
  border-radius: 25px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.first-dialog-submit .MuiDialog-paperWidthSm{
  width: 400px ;
  border-radius: 10px;
  padding: 20px 30px;
}

.main-dialog-heading{
  font-size: 14px;
  font-weight: 700;
  padding-bottom: 10px;
  color: #000;
}

.main-dialog-sub-heading{
  font-size: 11px;
  font-weight: 500;
  padding-bottom: 20px;
  color: #000;
}

.main-dialog-buttons-wrapper{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dialog-retry-button{
  width: 49% ;
  text-transform: none ;
  font-size: 14px ;
  color: #04424d;
  background-color: #fff ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 4px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 1px solid #04724d;
}

.dialog-submit-button{
  width: 49% ;
  text-transform: none ;
  font-size: 14px ;
  color: #fff ;
  background-color: #04724d ;
  font-weight: bold ;
  padding: 5px;
  border-radius: 4px;
  height: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: 1px solid #04724d;
}

.question-text{
  font-size: 15px;
  margin-top: 20px;
}

.first-dialog-submit .MuiDialog-container{
  height: 40% ;
}

.audio-analyser-div{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 200px;
}
.audio-img{
  margin-right: 10px;
}
.video-div{
  display: flex;
  justify-content: center;
  height: 360px;
}
.mainPage1 {
  padding: 0px 50px;
  /* display: flex;
  justify-content: center !important; */
}

.timer-2{
  display: none !important;
}

.interview-flex-div {
  display: flex;
  justify-content: space-between;
}
.start-btn {
  display: block;
}
.total-questions{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.interview-video-new {
  position: relative;
  height: 100%;
  width: 630px;
}
.interview-video-new video {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
}
.controls {
  display: none;
}
.candidateName {
  font: normal normal 600 29px/43px Ubantu;
  position: absolute;
  top: 0px;
  left: 25px;
  margin-top: 10px;
  opacity: 1;
}
.candidateName span {
  margin-left: 10px;
}
.question-div {
  width: 100%;
  text-align: center;
}
.read-timer {
  display: flex;
  justify-content: space-between;
}
.timing {
  position: absolute;
  bottom: 0px;
  font-size: 28px;
  font-weight: bold;
  right: 0px;
  margin-right: 15px;
  letter-spacing: 0px;
  color: #f4f9f5;
  margin-bottom: 30px;
}
.step-label {
  color: black;
}
.circle-interview {
  /* position: absolute; */
  text-align: center;
  /* right: 0; */
  margin-left: auto;
  height: 40px;
  width: 40px;
  font-size: 20px;
  padding: 5px;
  background-color: #cadedb;
  border-radius: 50%;
  float:right
}
.questions-wrapper{
  width: 180px;
  display: flex;
  border: 1px solid gray;
  border-radius: 27px;
  padding-left: 10px;
  background: white;
  align-items: center;
}

.total{
  font-size: 13px;
}
.previews-heading .MuiTypography-body1 {
  font-size: 34px;
  font-weight: bolder;
  color: #04724d;
  text-shadow: 0px 1px, 1px 0px, 1px 1px;
  letter-spacing: 1.5px;
}
.content-div {
  padding: 40px 10px 10px 10px;
}
.skipnextButton {
  display: flex;
  justify-content: space-evenly;
}

.qustion-content-div {
  padding: 20px 0;
}

.question {
  font-size: 26px;
  line-height: 40px;
  font-weight: bold;
  text-align: left;
}

.question-div .MuiPaper-root {
  background: transparent;
  width: fit-content;
}

.question-div .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.question-div .MuiStepConnector-alternativeLabel {
  display: none;
}

.question-div .MuiSvgIcon-root {
  width: 2em;
  height: 2em;
}

.skipButton,
.nextButton {
  width: 43%;
  margin: 0 20px;
}

.skipButton .MuiButton-contained {
  background: transparent;
  color: #04724d;
  padding: 12px;
  width: 100%;
  font-size: 18px;
  border: 2px solid #04724D;
  font-weight: bold;
  border-radius: 20px;
}

.nextButton .MuiButton-contained {
  border-radius: 20px;
  background-color: #04724d;
  border: 1px solid #04724D;
  color: #ffffff;
  padding: 5px 12px 5px 12px;
  font-size: 18px;
  width: 100%;
}
.timingCounting {
  background-color: #ffffff;
  width: 20%;
  border-radius: 25px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  text-align: center;
  padding: 10px 0px;
  margin: 0px 0px 20px 15px;
  color: #04724d;
  font-size: 20px;
  font-weight: bold;
}
.alarmIcon {
  margin-right: 10px;
}

.nextButton .MuiButton-contained:hover {
  background-color: #04724d;
}

.question-div .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.question-div .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.question-div .MuiStepIcon-root {
  color: #04724d1a;
}

.Button {
  background-color: #04724d;
  color: #ffffff;
}

.buttons-wrapper{
  display: flex;
  align-items: center;
  margin-left: 20px;

}

.Button-next-disabled{
  height: 34px !important;
  text-transform: none !important;
  margin-right: 10px !important;
  border-radius: 4px !important;
  font-size: 15px !important;
 background: #00000029 ; 
  color: #fff !important;
}

.Button-next,.Button-retry{
  /* width: 200px !important; */
  height: 34px !important;
  text-transform: none !important;
  margin-right: 10px !important;
  border-radius: 4px !important;
  font-size: 15px !important;
  cursor: pointer;
}


.Button-next{
  background: #04724d !important;
  color: #fff !important;
}


.Button-retry{
  color: #04724d !important;
  background-color: transparent !important; 
  border: 1px solid #04724d !important;
}



.contents {
  margin-top: 20px;
  padding: 50px;
  border-radius: 30px;
}

.container{
  display: flex;
}
.total-questions-new{
  display: none;
}

.interview-row-wrapper{
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.interview-row-wrapper-new{
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
}

.inter-1{
  width: 50%;
}

.inter-2{
  width: 50%;
}
.left-interview{
  width: 50%;
}

.right-interview{
  width: 40%;
  margin: 0 auto;
}


@media(max-width:1024px){
  .total{
    font-size: 11px;
  }
  .interview-main-div{
    padding: 0px 20px;
    background: #fff;
    height: 100vh;
  }
  .total-questions{
    display: none;
  }
  .total-questions-new{

    padding: 20px 0;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    transform: translate(-50%, 0);

  }
  .inter-1{
    width: 100%;
  }
  
  .inter-2{
    width: 0%;
  }
  .interview-row-wrapper{
   flex-direction: column;
   margin-bottom: 0;
  }
  .interview-video-new{
    background: #fff;
  }
  .interview-video-new video{
    height: 400px !important;
    border-radius: 0;
    width: 768px;
    object-fit: cover;
object-position: center
    

  }
  .left-interview{
    width: 100%;
  }
  
  .right-interview{
    width: 100%;
  }

  .questions-wrapper{
    width: 135px;
    /* padding: 5px; */
  }
  .circle-interview{
    width: 35px;
    height: 35px;
    font-size: 16px;
    
  }

  .timer-1{
    display: none !important;

  }

  .timer-2{
    display: block !important;
    margin: 0 15px;
 

  }

  .interview-logo{
    display: none !important;
  }
  .content-div{
    padding: 10px ;
  }
  .qustion-content-div{
  padding: 0px;
  }

  .laptop-only{
    display: none !important;
  }

  .question{
    font-size: 15px;
    line-height: 20px;
  }
}

@media(max-width:765px){
  .interview-video-new video{
    border-radius: 0;
    width: 360px;
    object-fit: cover;
    object-position: center
  }
}
